export default class NotificationConfig {
    public static getErrorsConfig(message: string, title: string = '!') {
        return {
            group: 'error-message',
            type: 'error',
            title,
            text: message,
        };
    }

    public static getSuccessConfig(message: string, title: string = '!') {
        return {
            group: 'success-message',
            type: 'success',
            title,
            text: message,
        };
    }
}
